.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    z-index: 2;
}
  
.card > .img-container {
height: 100%;
overflow: hidden;
text-align: center;
background:#6CADDC;
}
.card > .img-container > img {
width: 100%;
height: 100%;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05); 
}

.wrapper {
  background: #ccc;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 0 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
    width: 100%;
    height: 100px;
    text-align: center;
    font-size: 30px;
    color: #ccc;
    margin-bottom: 50px;
    padding-left: 3%;
    padding-right: 3%;
    background: black;
    position: fixed;
    z-index: 3;
  }
.logo {
    margin-top: 120px;
    background-image: url("https://images.launchbox-app.com/540e437b-2a44-496c-9653-7090f6632af2.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 500px;
    width: 100%;
}

footer {
    font-size: 20px;
    text-align: center;
    color: #ccc;
    position: relative;
    padding: 0px;
    width: 100%;
    bottom: 0;
    height: 50px;
    background: black;
}
